import React, { useState, useEffect } from "react";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";

const Manufacturer = ({ match }) => {
  const [vendersData, setVendersData] = useState(null);
  const { token } = useSelector(selectUser);

  useEffect(() => {
    axios
      .post(
        process.env.REACT_APP_SERVER + "/api/ultiModealTeam/listingAllProducts",
        {
          createdBy: match.params.id,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((data) => {
        setVendersData(data.data.vendor);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [token, match.params.id]);
  console.log(vendersData);

  return <div>{match.params.id}</div>;
};

export default Manufacturer;
